.experience_container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem;
}

.experience_container > div {
  background: var(--color-bg-varieant);
  padding: 2.4rem 5rem;
  border-radius: 2rem;
  border: 1px solid transparent;
  transition: var(--transition);
}

.experience_container > div:hover {
  background: transparent;
  border-color: var(--color-bg-varieant);
  cursor: default;
}

.experience_container > div h3 {
  text-align: center;
  margin-bottom: 2rem;
  color: var(--color-primary);
}

.experience_content {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.experience_details {
  margin: auto;
  margin-top: 0.8rem;
  height: 50%;
  width: 50%;
}

/* .detais-icon {
  margin-top: 0.4rem;
  color: var(--color-primary)
} */

.frase {
  text-align: center;
  color: var(--color-light);
  margin-top: 1.5rem;
}



@media screen and (max-width: 1024px) {
  .experience_container {
    grid-template-columns: 1fr;
  }

  .experience_container > div {
     width: 80%;
     padding: 2rem;
     margin: 0 auto;
  }

  .experience_content {
    padding: 1rem;
  }
}

/* ----media queries (small devices)--- */

@media screen and (max-width: 600px) {
  .experience_container {
    gap: 1rem;
  }

  .experience_container > div {
    width: 100%;
    padding: 2rem 1rem;

 }
}

