.about_container {
  
  display: grid;
  grid-template-columns: 35% 50%;
  gap: 15%;
}

.about_me {
  width: 100%;
  aspect-ratio: 1/1;
  border-radius: 2rem;
  background: linear-gradient(45deg, transparent, var(--color-primary));
  display: grid;
  place-items: center;
}

.about_me-image {
  border-radius: 2rem;
  overflow: hidden;
  transform: rotate(10deg);
  transition: var(--transition)
}

.about_me-image:hover {
  transform: rotate(0deg);
}

.about_content p {
  margin: 2rem 0 2.6rem;
  color: var(--color-light);
  text-align: justify
}

/* ----media queries (medium devices)--- */

@media screen and (max-width: 1024px) {
  .about_container {
    margin-top: 50%;
    grid-template-columns: 1fr;
    gap: 0;
  }

  #about {
    margin-top: 75%;
  }

  .about_me {
    width: 50%;
    margin: 2rem auto 4rem;
  }

  .about_content p {
    margin: 1rem 0 1.5rem;
    color: var(--color-light);
  }
}

/* ----media queries (small devices)--- */

@media screen and (max-width: 600px) {
  .about_me {
    width: 65%;
    margin: 0 auto 3rem;
  }

  .about_content {
    text-align: center;
  }

  .about_content p {
    margin: 1.5rem 0;
  }
}